.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.loader {
  letter-spacing: 2px;
  color: #9e0c0c;
  text-shadow: 0 0 1px #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
}

.loader:before {
  content: "";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

